import React from 'react';
import AdminIcon from '@mui/icons-material/AdminPanelSettingsTwoTone';
import TextField from '@mui/material/TextField';
import Typo from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconProgress from 'components/CircularProgress';
import PersonAddAltTwoToneIcon from '@mui/icons-material/PersonAddAltTwoTone';
import {
  Alert,
  FormControl,
  IconButton,
  InputLabel,
  MenuItem,
  Select,
  Snackbar
} from '@mui/material';
import { accessLevels, GetAccessLevelName } from 'utils/accessLevels.util';
import ArrowBackIos from '@mui/icons-material/ArrowBackIos';
import { useParams } from 'react-router-dom';

export function CreateView(props) {
  const params = useParams();
  let user = undefined;
  let acc = -1;
  if (params.id) {
    user = props.admins.find((i) => i._id === params.id);
    acc = accessLevels.findIndex((i) => i.name === GetAccessLevelName(user.Access));
    console.log(user);
  }
  const [circularValue, setValue] = React.useState(!user ? 0 : 100);
  const [username, setUsername] = React.useState(!user ? '' : user.Username);
  const [password, setPassword] = React.useState(!user ? '' : '*****');
  const [access, setAccess] = React.useState(acc);

  const [snack, setSnack] = React.useState('');
  const [message, setMessage] = React.useState('');

  const SetUsername = (e) => {
    setUsername(e.target.value);
    calculateProgress();
  };
  const ValidateUsername = (e) => {
    const response = props.admins.find((item) => item.Username === e.target.value);
    if (response === null || response === undefined) {
      SetUsername(e);
    } else {
      setUsername('');
    }
  };
  const SetPassword = (e) => {
    setPassword(e.target.value);
    calculateProgress();
  };
  const SetAccess = (e) => {
    setAccess(e.target.value);
    calculateProgress();
  };

  const calculateProgress = () => {
    let v = 0;
    if (username) v += 25;
    if (password) v += 25;

    if (access > -1) v += 50;
    setValue(v);
  };

  const inputProps = {
    variant: 'filled',
    color: 'primary',
    className: 'my-3 mx-0 w-100'
  };

  return (
    <div className="row">
      <div className="col-12">
        <div className="row">
          <div className="col-12 text-start">
            <IconButton onClick={props.onGoBack}>
              <ArrowBackIos fontSize="small" />
            </IconButton>
            <hr />
          </div>
        </div>
        <div className="row">
          <div className="col-md-6 text-center">
            <IconProgress
              color="success"
              className={`rounded-circle ${
                circularValue === 100 ? 'shadow' : circularValue >= 50 ? 'shadow-sm' : ''
              }`}
              value={circularValue}>
              <AdminIcon
                fontSize={circularValue === 100 ? 'large' : 'medium'}
                color={circularValue === 100 ? 'success' : 'inherit'}
              />
            </IconProgress>
            <Typo align="center" variant="h5">
              {!username ? 'تعریف ادمین' : username}
            </Typo>
            <Typo align="center" variant="caption">
              {access === -1 ? '' : accessLevels[access].name}
            </Typo>
          </div>
          <div className="col-md-6">
            <div className="row">
              <div className="col-12">
                <TextField
                  value={username}
                  onChange={SetUsername}
                  onBlur={ValidateUsername}
                  {...inputProps}
                  label="نام کاربری"
                />
              </div>
            </div>
            <div className="row">
              <div className="col-12">
                <TextField
                  value={password}
                  onChange={SetPassword}
                  {...inputProps}
                  disabled={!!user}
                  label="رمز عبور"
                />
              </div>
            </div>
            <div className="row">
              <div className="col-12">
                <FormControl {...inputProps}>
                  <InputLabel id="ac">سطح دسترسی</InputLabel>
                  <Select
                    labelId="ac"
                    id="ac-select"
                    value={access}
                    label="سطح دسترسی"
                    onBlur={SetAccess}
                    onChange={SetAccess}>
                    {accessLevels.map((item, index) => (
                      <MenuItem key={`AC=${index}`} value={index}>
                        {item.name}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </div>
            </div>
            <div className="row">
              <div className="col-12">
                <Button
                  variant="outlined"
                  color="success"
                  className="mx-0"
                  disabled={circularValue !== 100}
                  onClick={() => {
                    props
                      .onCreate({
                        Username: username,
                        Password: password,
                        Access: accessLevels[access].detail
                      })
                      .then((r) => {
                        const [status, code] = r;
                        if (status) {
                          setMessage('ثبت شد !');
                          setSnack('success');
                          setUsername('');
                          setPassword('');
                          setAccess(-1);
                          calculateProgress();
                        } else {
                          setMessage('خطا در ارسال اطلاعات. کد ' + code);
                          setSnack('error');
                        }

                        setTimeout(() => {
                          setMessage('');
                          setSnack('');
                        }, 5000);
                      });
                  }}>
                  ثبت
                  <PersonAddAltTwoToneIcon fontSize="small" className="me-2" />
                </Button>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <Snackbar
              anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
              open={snack !== ''}>
              <Alert severity={!snack ? 'info' : snack} sx={{ width: '100%' }}>
                {message}
              </Alert>
            </Snackbar>
          </div>
        </div>
      </div>
    </div>
  );
}
export default CreateView;
